// Enhanced Constants
const AD_FREQUENCY = 8;
const SWIPE_THRESHOLD = 100;
const SWIPE_VELOCITY_THRESHOLD = 500;
const SWIPE_OUT_DISTANCE = 500;
const ROTATION_FACTOR = 0.08;
const MAX_ROTATION = 15;
const CARD_WIDTH = 350;
const CARD_HEIGHT = 750;
const STACK_OFFSET = 8;
const SCALE_FACTOR = 0.03;

const { width } = { width: typeof window !== 'undefined' ? window.innerWidth : 400 };
const circleDiameter = Math.min(width * 0.9, 500);

// Animation variants
const cardVariants = {
    enter: (index: number) => ({
        scale: 1 - index * SCALE_FACTOR,
        y: index * STACK_OFFSET,
        rotateY: index * -2,
        opacity: 1,
        zIndex: 10 - index,
        transition: {
            type: "spring",
            stiffness: 300,
            damping: 30,
        }
    }),
    exit: (direction: number) => ({
        x: direction > 0 ? SWIPE_OUT_DISTANCE : -SWIPE_OUT_DISTANCE,
        rotateZ: direction > 0 ? 30 : -30,
        opacity: 0,
        scale: 0.8,
        transition: {
            duration: 0.4,
            ease: [0.25, 0.46, 0.45, 0.94]
        }
    })
};

